import React from 'react';
import Suggestions from "./Suggestions";
import Autosuggest from 'react-autosuggest';
import AutosuggestHighlightMatch from 'autosuggest-highlight/match';
import AutosuggestHighlightParse from 'autosuggest-highlight/parse';
import { Image } from 'react-bootstrap';
import '../css/AutoSuggest.css';

function getMoviesFromMongoDB(type, context) {
  var url = "https://gdjactscaf.execute-api.us-east-1.amazonaws.com/Prod/retrieveMovies?";
  const title = context.title;
  const query = context.query;
  const number = context.number;
  const genre = context.genre;
  const start = context.start;
  const end = context.end;

  if (type === "find_one") {
    url = url + "type=" + type + "&title=" + title;
  }
  else if (type === "find_many") {
    url = url + "type=" + type + "&query=" + query + "&number=" + number;
  }
  else {
    url = url + "type=" + type + "&genre=" + genre + "&start=" + start + "&end=" + end;
  }

  

  return fetch(url)
        .then(res => res.json())
        .then(
          (result) => {
            // console.log(result);
            return result;
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            alert(error);
          }
         );
}

